.container {
  width: auto;
  padding: 0 16px;
  margin-bottom: 50px;
}
.layout {
  min-height: 300px;
  padding: 50px 0;
  background: #ffffff;
  min-height: 600px;
}
.block {
  min-width: 1200px;
  height: 224px;
  display: table;
  margin: 30px auto 0;
  border-radius: 5px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);
}
