.container {
  width: auto;
  padding: 0 16px;
  margin-bottom: 50px;
}
.layout {
  min-height: 300px;
  padding: 50px 0;
  background: #f6fafe;
}
.ulDiv {
  min-width: 600px;
  position: relative;
}
.ulDiv li {
  /*&:active {
    background-color: yellow;
  }*/
}
.liDiv {
  display: inline-block;
  min-width: 16.66667%;
  height: 390px;
  background: #fff;
  box-shadow: 0 10px 30px 0 rgba(18, 154, 231, 0.12);
  transition: all 0.4s ease-in;
}
.liDiv:hover {
  min-width: 400px;
}
.active {
  min-width: 400px;
  background: #00aaef;
}
