.container {
  width: auto;
  padding: 0 16px;
  margin-bottom: 50px;
}
.layout {
  min-height: 300px;
  padding: 50px 0;
  background: #2558ff;
  color: white;
}
.global {
  position: relative;
  min-height: 560px;
}
.left {
  position: absolute;
  background: #1890ff;
  width: 800px;
  height: 100%;
  top: 0;
  left: 0;
}
.right {
  position: absolute;
  background: #DEC811;
  width: 686px;
  height: 100%;
  top: 0;
  right: 0;
  overflow: hidden;
}
.L_liDiv {
  position: relative;
  width: 100px;
  height: 100px;
  background: #27ffc0;
  margin: 10px;
  animation-duration: 3s;
  animation-name: L_liDiv;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
@keyframes L_liDiv {
  from {
    top: 0;
  }
  50% {
    top: 20px;
  }
  to {
    top: 0;
  }
}
.R_liDiv {
  position: absolute;
  width: 80%;
  height: 100%;
  opacity: 0;
  transition: all 0.5s ease;
  right: -80%;
}
.active {
  opacity: 1;
  right: 0;
  -webkit-transition-delay: 0.5s;
}
.li1 {
  left: 32.5%;
  top: 17%;
  animation-delay: 0.7s;
}
.li2 {
  left: 23%;
  top: 47%;
  animation-delay: 0.2s;
}
.li3 {
  left: 64%;
  top: 17%;
  animation-delay: 0.5s;
}
